<template>
	<!-- #ifdef MP-WEIXIN -->
	<view class="status_bar" :style="{
		height:getClass.statusBarHeight+'px'
	}">
		<!-- 这个是导航栏的 -->
	</view>
	<!-- #endif -->
	
</template>

<script>
	export default{
		computed:{
			// 适配
			//  #ifdef MP-WEIXIN
			getClass(){
				let data = wx.getMenuButtonBoundingClientRect()
				let req;
				wx.getSystemInfo({
					success: res => {
					req = res
				  }
				})
				return {
					...data,
					statusBarHeight:req.statusBarHeight + 4,
				}
			
			},
			// #endif
		}
	}
</script>



<style lang="scss">
	.status_bar{
		border: none !important;
		background-color:rgba(0,0,0,0);
		z-index:$uni-zindex-lg;
	}
</style>